body {
    background: transparent url(./css/background.jpeg) no-repeat fixed center center/cover;
    margin: 0 auto;
    /* height: 60vh; */

    /* position: relative; */
}
#header-title{
  background: -webkit-linear-gradient(90deg,#ffd301 0%,#f4702a 21%,#ee254a 40%,#ed2870 60%,#c52378 80%,#861766 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-right: 20px;
  margin-bottom: 0px;
  margin-top: 10px;
}

@media only screen and (max-width: 330px) {
  #header-title{
    font-size: 25px;
  }
}

#main-header{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

#main-navbar{
width: 10%;
height: 65px;
}

.content-container{
  min-height: calc(100vh - 45px);
}

