/* login */
.login-form-container{
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 90%;
  background-color: #FFF;
  border-radius: 10px;
  box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 9px 26px 0 rgba(0, 0, 0, 0.19);
}

@media only screen and (min-width: 600px) {
  .login-form-container{
    width:40%;
  }
}


.login-form-div{
  display: flex;
  justify-content: center;
  /* height: 80VH; */
}

.login-divider{
  margin-top: 20px;
  margin-bottom: 10px;
}
/* Homepage */
#homepage-h1{
  margin-bottom: 25px;
  margin-top: 25px;
}

@media only screen and (min-width: 768px){
#homepage-h1 {
    margin-bottom: 40px;
    margin-top: 40px;
  }
}

.homepage-container{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  align-content: center;
}
.homepage-buttons{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

@media only screen and (min-width: 760px) {.homepage-buttons{
  width:60%
  }}

@media only screen and (min-width: 915px) {.homepage-buttons{
  width:50%
  }}

@media only screen and (min-width: 1200px) {.homepage-buttons{
  width:35%
  }}

.homepage-link{
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;

}

.btn-circle.btn-xl {
  width: 100px;
  height: 100px;
  padding: 10px 16px;
  font-size: .9rem;
  font-weight: bold;
  line-height: 1.33;
  border-radius: 50%;
  margin: 5px;
}

@media only screen and (max-width: 330px){
.btn-circle.btn-xl {
  width:95px;
  height:95px;
  }
}

/* PROFILE */
#profile-h2{
  font-size: 1.5rem;
  background: -webkit-linear-gradient(90deg,#ffd301 0%,#f4702a 21%,#ee254a 40%,#ed2870 60%,#c52378 80%,#861766 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.profile-form-div{
  display: flex;
  justify-content: center;
}

.profile-form-container{
  width: 80%;
  line-height: .75rem;
  padding-top: 10px !important;
  padding-bottom: 5px !important;
}

@media only screen and (min-width: 600px) {
  .profile-form-container{
    width:40%;
  }
}

.profile-form-label{
  font-size: .8rem;
  color: #CA344F;
  font-weight: bold;

}

h1{
  text-align: center;

}

.ins-card{
  width: 220px;
  border-radius: 10px;
}

.ins-card-modal{
  max-width: 100%;
  border-radius: 10px;
}

.profile-modal-body{
  display: flex;
  justify-content: center;
}

#ins-card-container{
  display: flex;
  flex-direction: column;
}

.edit-profile-button{
  font-size: 16px;
}

input{
  border: none;
  border-bottom: 1px solid #2D3338;
  width:250px;
  text-align: center;
}

.profile-input{
  width: 200px;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active
{
 -webkit-box-shadow: 0 0 0 30px white inset !important;
}


input:disabled {
/* input[type="text"]:disabled { */
  /* border-radius: 10px; */
  background: #FFF;
  -webkit-box-shadow: 0 0 0 30px white inset !important;
  text-align: center;

}

input[type="text"]:selection {
  /* border: 1px solid blue; */
  /* border-radius: 10px; */
}

.profile-button{
  border:none;
  padding: 0px;
  font-size: 1.2rem;

}

/* RECORDS */
.record-form-div{
  display: flex;
  justify-content: center;
}

ul{
  list-style-type: none;
  padding: 0px;
  margin: 0px;
}

.li-record{
  border: solid black 1px;
  width: 300px;
  text-align: center;
  margin: 10px;
}

.appt-img{
  width: 220px;
}

.appt-filter-container{
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.category-button{
  margin: 2px;
}


/* Add Record- modal */
.addrecord-button-container{
  display: flex;
  justify-content: center;
  margin: 20px;

}

.addRecord-form-container{
  width: 90%;
  line-height: .75rem;
  /* padding-top: 10px !important;
  padding-bottom: 5px !important; */
  padding: 0px;
  margin-top: 10px;
}

@media only screen and (min-width: 600px) {
  .addRecord-form-container{
    width:40%;
    margin-top: 100px;
  }
}


/* Record Detail */

.recordDetail-form-container{
  width: 80%;
  line-height: .75rem;
  /* padding-top: 10px !important;
  padding-bottom: 5px !important; */
  padding: 0px;
  margin: 0px;
}

.record-detail-button{
  margin: 2px;
  border:none;
  padding: 0px;
  font-size: 1.2rem;
}

.record-form-label{
  font-size: .8rem;
  color: #CA344F;
  font-weight: bold;
}

.record-input{
  width: 215px;
}

.record-input-date{
  padding-left: 33px;
}

select:disabled{
opacity: unset;
background: unset;
border: unset;
border-bottom: solid 1px #2D3338;
width: 215px;
text-align: center;
color: rgb(84, 84, 84);
-webkit-appearance: none;
padding-left: 60px;
padding-bottom: 2px;
line-height: 1.2rem;
}

#xrays{
  width: 215px;
}

.record-detail-button{
  border: none;
  padding: 0px;
  font-size: 1.2rem;
  box-shadow: unset !important;
}

.record-detail-button:seleted{
  box-shadow: unset;
}

/* Find Care */
.map{
  max-width: 200px;
  max-height: 200px;
}


.findcare-container{
  display: flex;
  justify-content: center;
}

.findcare-locationsList-container{
  display: flex;

}
